<template>
    <ion-page>
        <ion-header>
            <ion-toolbar>
                <ion-title class="his-lg-text">
                    <b>{{ identifier }}</b>
                </ion-title>
            </ion-toolbar>
        </ion-header>
        <ion-content>
            <ion-list>
                <ion-item class="his-md-text" v-for="(option, index) in options" :key="index">
                    <ion-label>
                        {{ option.label }}
                    </ion-label>
                    <ion-label slot="end">
                        <b>{{ option.value }}</b>
                    </ion-label>
                </ion-item>
            </ion-list>
        </ion-content>
        <ion-footer>
            <ion-toolbar color="default" style="padding: 5px;">
                <ion-button router-link="/home" size="large">
                    Cancel
                </ion-button>
                <ion-button size="large" slot="end" @click="printAccessNumber">
                    Print Accession Number
                </ion-button>
                <ion-button @click="addTest" :disabled="!orderIsset" slot="end" size="large">
                    Add test
                </ion-button>
                <ion-button v-if="orderStatus !== 'specimen-accepted' && orderStatus !== 'specimen-rejected'"
                    @click="changeSpecimenStatus('accepted')" :disabled="!orderIsset" slot="end" size="large"
                    color="success">
                    Accept
                </ion-button>
                <ion-button v-if="orderStatus !== 'specimen-accepted' && orderStatus !== 'specimen-rejected'"
                    @click="changeSpecimenStatus('rejected')" :disabled="!orderIsset" slot="end" size="large"
                    color="danger">
                    Reject
                </ion-button>
            </ion-toolbar>
        </ion-footer>
    </ion-page>
</template>
<script setup lang="ts">
import { Option } from "@/components/Forms/FieldInterface";
import {
    IonList,
    IonLabel,
    IonTitle,
    IonPage,
    IonHeader,
    IonToolbar,
    IonContent,
    IonFooter,
    IonButton,
    IonItem
} from "@ionic/vue"
import { computed, onMounted, ref, watch } from "vue";
import { useRoute } from "vue-router";
import Orders from "@/composables/orders"
import { MultiStepPopupForm } from "@/utils/PopupKeyboard"
import { FieldType } from "@/components/Forms/BaseFormElements";
import Validation from "@/components/Forms/validations/StandardValidations"
import { Client } from "@/composables/client";
import { toastSuccess, toastWarning } from "@/utils/Alerts";
import { modalController } from "@ionic/core";
import router from "@/router";
import PrintService from "@/services/print_service";

const client = Client()

const route = useRoute()

const { searchResult } = Orders()

const clientID = ref('')

const orderStatus = ref<string>('')

const options = ref<Option[]>([
    { label: 'Order Id', value: '-' },
    { label: 'Tracking Number', value: '-' },
    { label: 'Accession Number', value: '-' },
    { label: 'Patient Name', value: '-' },
    { label: 'Specimen Type', value: '-' },
    { label: 'Test(s) Ordered', value: '-' },
    { label: 'Specimen Status', value: '-' },
])

const identifier = `${route.params.identifier || 'Unknown ID'}`

const orderIsset = computed(() => Object.keys(searchResult.value).length ? true : false)


function loadOrder() {
    Orders().searchbyID(identifier)
}

async function changeSpecimenStatus(status: string): Promise<void> {

    const res = await client.putJson(`order_statuses/${status}`, {
        order_id: options.value[0].value
    })

    if (res?.ok) {
        toastSuccess(`Specimen ${status} successfully!`)
        router.push('/tests')
    }

}

function addTest() {
    MultiStepPopupForm([
        {
            id: "test_types",
            helpText: "Select test types",
            type: FieldType.TT_MULTIPLE_SELECT,
            config: {
                showKeyboard: true
            },
            computedValue: (val: Option[], f: any) => {
                return {
                    order_id: searchResult.value?.order_id || 0,
                    tests: val.map((v: Option) => {
                        return {
                            specimen: v.value,
                            test_type: v.label
                        }
                    })
                }
            },
            options: async () => {
                const [test] = searchResult.value?.tests || []
                if (!test) return []
                const res: any = await client.getJson('specimen/test_types', {
                    'specimen_id': test.specimen_id as number
                })
                if (!res?.ok) return []
                return res.data.filter(
                    (test: string) => !searchResult.value.tests.map((r: any) => r.test_type).includes(test)
                ).map((testType: string) => ({
                    label: testType,
                    value: test.specimen_id
                }));
            },
            validation: (val: Option) => Validation.required(val)
        }],
        async (_: any, payload: any) => {
            const req: any = await client.postJson('orders/add_test_to_order', payload.test_types)
            if (!req.ok) return toastWarning(req.error)
            modalController.getTop().then((v: any) => v && modalController.dismiss())
            loadOrder()
        }, null)
}

watch(searchResult, async (data) => {
    if (Object.keys(data).length) {

        const res = await client.getJson(`orders/${data.order_id}`)

        orderStatus.value = res?.data.order_status

        clientID.value = data.client_id
        const [test] = data.tests
        options.value = [
            { label: 'Order Id', value: data.order_id },
            { label: 'Tracking Number', value: data.tracking_number },
            { label: 'Accession Number', value: data.accession_number },
            { label: 'Patient Name', value: `${data.first_name} ${data.last_name}` },
            { label: 'Specimen Type', value: test.specimen },
            { label: 'Test(s) Ordered', value: data.tests.map((t: any) => t.test_type).join(', ') },
        ]
    }
}, { deep: true, immediate: true })

const printAccessNumber = (): void => {
    const printService = new PrintService()
    const { value: accession_number = '-' } = options.value.find((option: { label: string; }) => option.label === 'Accession Number') || {};
    printService.writeLbl('printout/accession_number', { accession_number }, `${accession_number}.lbl`);
}

onMounted(() => {
    if (!orderIsset.value) loadOrder()
})
</script>
